import { React, useEffect, useState } from 'react';
import { useNavigate, useLocation } from 'react-router-dom';
import Button, { ButtonGroup } from '../../../../components/bootstrap/Button';
import Card from '../../../../components/bootstrap/Card';
import Page from '../../../../layout/Page/Page';
import { DropdownItem, DropdownMenu, DropdownToggle } from '../../../../components/bootstrap/Dropdown';
import Modal, { ModalBody, ModalFooter, ModalHeader, ModalTitle } from '../../../../components/bootstrap/Modal';
import Dropdown from '../../../../components/bootstrap/Dropdown';
import PageWrapper from '../../../../layout/PageWrapper/PageWrapper';
import { cirurgicalFlow } from '../../../../menu';
import Header from '../../../../components/Header';
import API from '../../../../Services'

import './style.css'
import AssinaturaUsuario from '../../../../components/Assinatura';


const Limpeza = () => {



	const navigate = useNavigate()
	const location = useLocation()

	const [abrirModalAssinatura, setAbrirModalAssinatura] = useState(false)
	
	const [dados, setDados] = useState(null)
	// eslint-disable-next-line no-unused-vars
	const [modalAlerta, setModalAlerta] 				= useState(false)
	// eslint-disable-next-line no-unused-vars
	const [modalMessage, setModalMessage]				= useState({ COR: '', MSG: '', TITULO: '' })
	// eslint-disable-next-line no-unused-vars
	const [time, setTime] 								= useState(Date.now())
	const [perguntas, setPerguntas] 					= useState([])
	// eslint-disable-next-line no-unused-vars
	const [respostas, setRespostas] 					= useState([])
	// eslint-disable-next-line no-unused-vars
	const [inputInicioPreparo, setInputInicioPreparo] 	= useState('')
	// eslint-disable-next-line no-unused-vars
	const [inputFimPreparo, setInputFimPreparo] 		= useState('')
	// eslint-disable-next-line no-unused-vars




	const session = JSON.parse(localStorage.getItem('CAPP-local'))
	const localidadeHeader = `Sala ${ session.SUBLOCALIDADE === 'Hemodinamica' ? session.SUBLOCALIDADE : session.SUBLOCALIDADE.split(' ')[1]}`;

	// eslint-disable-next-line no-unused-vars
	const alterarLocalidade = () => {
		localStorage.removeItem('CAPP-local');
		navigate('/')
	};


	// eslint-disable-next-line no-unused-vars
	const avancarEtapaCirurgia = async (proxEtapa, dtAvanco) => {

		const responsavelRecebimento = JSON.parse(localStorage.getItem('responsavel_recebimento'))

		if (!responsavelRecebimento) {
			return false;
		}

		const params = {
			avisoCirurgia: location.state.AVISO,
			dtEtapa: dtAvanco,
			cdEtapa: proxEtapa,
			usuario: responsavelRecebimento.USUARIO,
			salaCirurgia: session.CODIGO_SALA_CIRURGICA,
			tipoAcao: 'I',
			sublocalidade: session.CODIGO_SUBLOCALIDADE
		}

		const avancarEtapaCirurgiaReturn = await API.post('/avancarEtapasCirurgia', params);

		if (!avancarEtapaCirurgiaReturn) {
			return false;
		}

		return true;

	};



	// Assinar datas
	const assinar = async (codEtapa) => {

		// Então registra o momento exato.
		const momentoAtual = new Date();
		momentoAtual.setMinutes(momentoAtual.getMinutes() - momentoAtual.getTimezoneOffset());
		const dataFormatada = momentoAtual.toISOString().slice(0, 16);

		navigate(`/salaCirurgica/AssinaturaLimpeza`, {
			state: {
				TIPO: 'NOVA',
				CODIGO_LOCALIDADE: session.CODIGO_LOCALIDADE,
				CODIGO_AVISO: location.state.AVISO,
				CODIGO_SUB_LOCALIDADE: session.CODIGO_SUBLOCALIDADE,
				CODIGO_SALA_CIRURGICA: session.CODIGO_SALA_CIRURGICA,
				ETAPA: codEtapa,
				DATA: dataFormatada,
				PAPEL_PERMITIDO: (codEtapa === 160 || codEtapa === 170) ? 222 : 156
			}
		})
		return true;
	}



	// eslint-disable-next-line no-unused-vars
	const retornaPerguntasChecklist = async () => {
		try {
			const requestPerguntas = await API.get(`/retornaPerguntasPreparoLimpeza/${location.state.AVISO}`);
			setPerguntas(requestPerguntas.data)
			// Se o fim tiver preenchida, redireciona o paciente à tela inicial. (Preparo e Limpeza)

			if (requestPerguntas.data[2].DT_ETAPA !== null) {
				localStorage.removeItem("avancouEtapa");
				navigate("/salaCirurgica/preparoLimpeza")
			}
			

		} catch (error) {
			console.log(error);
		}
	}



	useEffect(() => {

		console.warn('DEBUG')
		console.log('============= O QUE ESTÁ CHEGANDO POR PARÂMETRO  ==================================\n')
		console.log(location.state)
		console.log('============= FIM =================================================================\n')
		/*
				if ((!location.state)) {
					navigate('/appcc/')
				}
		*/
		// CHECA SE JÁ FOI REALIZADO O AVANÇO DE ETAPA DO PACIENTE.

		if(location.state.SAIDA_SALA === true)
		{
			if(location.state.CD_ETAPA === 110){
				avancarEtapaCirurgia(location.state.MOTIVO_ALTA, new Date());
			}else{
				avancarEtapaCirurgia(110, new Date());
				avancarEtapaCirurgia(location.state.MOTIVO_ALTA, new Date());
			}
			
		}

		/*
		if (!localStorage.getItem('avancouEtapa')) {
			localStorage.setItem('avancouEtapa', true);
			avancarEtapaCirurgia(110, new Date());
			avancarEtapaCirurgia(location.state.MOTIVO_ALTA, new Date());
		}
			*/
		localStorage.removeItem("responsavel_recebimento");


		retornaPerguntasChecklist()

		// Atualizar dados a cada 1 minuto.   
		const interval = setInterval(() => setTime(Date.now()), 30000);
		return () => {
			clearInterval(interval);
		};

	}, [location, navigate, time]);



	async function confirmacaoModalData(codigoEtapa, data) {
		// SE FOR SOLICITAÇÃO DE LIMPEZA
		if (codigoEtapa === 0) {
			if (perguntas[0].DT_ETAPA !== null) {
				const validaDataSaidaSala = await API.get(`/retornaHorarioSaidaSala/${location.state.AVISO}`);
				const dataRetornada = validaDataSaidaSala.data;

				if (new Date(data).getTime() < new Date(dataRetornada.SAIDA_SALA_SO).getTime()) {
					setModalAlerta(true);
					setModalMessage({ COR: '#FF7455', MSG: `A data de início não pode ser menor do que a saída de sala do paciente!`, TITULO: 'AVISO' });
					return false;
				}


				navigate(`/salaCirurgica/AssinaturaLimpeza`, {
					state: {
						TIPO: 'ALTERACAO',
						CODIGO_LOCALIDADE: session.CODIGO_LOCALIDADE,
						CODIGO_AVISO: location.state.AVISO,
						CODIGO_SUB_LOCALIDADE: session.CODIGO_SUBLOCALIDADE,
						CODIGO_SALA_CIRURGICA: session.CODIGO_SALA_CIRURGICA,
						ETAPA: codigoEtapa,
						DATA: data,
						PAPEL_PERMITIDO: 156
					}
				})

			} else {
				const validaDataSaidaSala = await API.get(`/retornaHorarioSaidaSala/${location.state.AVISO}`);
				const dataRetornada = validaDataSaidaSala.data;

				if (new Date(data).getTime() < new Date(dataRetornada.SAIDA_SALA_SO).getTime()) {
					setModalAlerta(true);
					setModalMessage({ COR: '#FF7455', MSG: `A data de início não pode ser menor do que a saída de sala do paciente!`, TITULO: 'AVISO' });
					return false;
				}


				navigate(`/salaCirurgica/AssinaturaLimpeza`, {
					state: {
						TIPO: 'NOVA',
						CODIGO_LOCALIDADE: session.CODIGO_LOCALIDADE,
						CODIGO_AVISO: location.state.AVISO,
						CODIGO_SUB_LOCALIDADE: session.CODIGO_SUBLOCALIDADE,
						CODIGO_SALA_CIRURGICA: session.CODIGO_SALA_CIRURGICA,
						ETAPA: codigoEtapa,
						DATA: data,
						PAPEL_PERMITIDO: 156
					}
				})



			}

		}




		// SE FOR INÍCIO DE LIMPEZA
		if (codigoEtapa === 160) {

			// É UMA ALTERAÇÃO OU A PRIMEIRA INSERSÃO?
			if (perguntas[1].DT_ETAPA !== null) {
				//alert('Você está alterando a data de início de limpeza.')

				// CHECA SE A DATA QUE ESTÁ SENDO INSERIDA É MENOR QUE A DATA DE SAÍDA DE SALA.
				const validaDataSaidaSala = await API.get(`/retornaHorarioSaidaSala/${location.state.AVISO}`);
				const dataRetornada = validaDataSaidaSala.data;

				if (new Date(data).getTime() < new Date(dataRetornada.SAIDA_SALA_SO).getTime()) {
					setModalAlerta(true);
					setModalMessage({ COR: '#FF7455', MSG: `A data de início não pode ser menor do que a saída de sala do paciente!`, TITULO: 'AVISO' });
					return false;
				}

				if (new Date(data).getTime() < new Date(perguntas[0].DT_ETAPA).getTime()) {
					setModalAlerta(true);
					setModalMessage({ COR: '#FF7455', MSG: `A data de início não pode ser menor do que a de solicitação da limpeza!`, TITULO: 'AVISO' });
					return false;
				}



				navigate(`/salaCirurgica/AssinaturaLimpeza`, {
					state: {
						TIPO: 'ALTERACAO',
						CODIGO_LOCALIDADE: session.CODIGO_LOCALIDADE,
						CODIGO_AVISO: location.state.AVISO,
						CODIGO_SUB_LOCALIDADE: session.CODIGO_SUBLOCALIDADE,
						CODIGO_SALA_CIRURGICA: session.CODIGO_SALA_CIRURGICA,
						ETAPA: codigoEtapa,
						DATA: data,
						PAPEL_PERMITIDO: 222
					}
				})
				return true;

			}

			//alert('Você está  inserindo uma data retroativa no início de limpeza.')

			// CHECA SE A DATA QUE ESTÁ SENDO INSERIDA É MENOR QUE A DATA DE SAÍDA DE SALA.
			const validaDataSaidaSala = await API.get(`/retornaHorarioSaidaSala/${location.state.AVISO}`);
			const dataRetornada = validaDataSaidaSala.data;

			if (new Date(data).getTime() < new Date(dataRetornada.SAIDA_SALA_SO).getTime()) {
				setModalAlerta(true);
				setModalMessage({ COR: '#FF7455', MSG: `A data de início não pode ser menor do que a saída de sala do paciente!`, TITULO: 'AVISO' });
				return false;
			}

			if (new Date(data).getTime() < new Date(perguntas[0].DT_ETAPA).getTime()) {
				setModalAlerta(true);
				setModalMessage({ COR: '#FF7455', MSG: `A data de início não pode ser menor do que a de solicitação da limpeza!`, TITULO: 'AVISO' });
				return false;
			}


			console.log(dataRetornada)

			navigate(`/salaCirurgica/AssinaturaLimpeza`, {
				state: {
					TIPO: 'NOVA',
					CODIGO_LOCALIDADE: session.CODIGO_LOCALIDADE,
					CODIGO_AVISO: location.state.AVISO,
					CODIGO_SUB_LOCALIDADE: session.CODIGO_SUBLOCALIDADE,
					CODIGO_SALA_CIRURGICA: session.CODIGO_SALA_CIRURGICA,
					ETAPA: codigoEtapa,
					DATA: data,
					PAPEL_PERMITIDO: 222
				}
			})
			return true;
		}

		// SE FOR FIM DE LIMPEZA
		if (codigoEtapa === 170) {
			// É UMA ALTERAÇÃO OU A PRIMEIRA INSERSÃO?
			if (perguntas[2].DT_ETAPA !== null)		// Se não possui uma data cadastrada no banco.
			{
				// A DATA INSERIDA NO FIM É MENOR DO QUE A DO INÍCIO?
				if (new Date(data).getTime() < new Date(perguntas[1].DT_ETAPA).getTime()) {
					setModalAlerta(true);
					setModalMessage({ COR: '#FF7455', MSG: `A data de fim não pode ser menor do que a data de início!`, TITULO: 'AVISO' });
					return false;
				}



				//alert('Você está alterando a data de fim de limpeza.')
				navigate(`/salaCirurgica/AssinaturaLimpeza`, {
					state: {
						TIPO: 'ALTERACAO',
						CODIGO_LOCALIDADE: session.CODIGO_LOCALIDADE,
						CODIGO_AVISO: location.state.AVISO,
						CODIGO_SUB_LOCALIDADE: session.CODIGO_SUBLOCALIDADE,
						CODIGO_SALA_CIRURGICA: session.CODIGO_SALA_CIRURGICA,
						ETAPA: codigoEtapa,
						DATA: data,
						PAPEL_PERMITIDO: 222
					}
				})
				// eslint-disable-next-line no-else-return
				return true;
				// eslint-disable-next-line no-else-return
			} else {
				if (new Date(data).getTime() < new Date(perguntas[1].DT_ETAPA).getTime()) {
					setModalAlerta(true);
					setModalMessage({ COR: '#FF7455', MSG: `A data de fim não pode ser menor do que a data de início!`, TITULO: 'AVISO' });
					return false;
				}

				//alert('Você está  inserindo uma data retroativa no fim de limpeza.')
				navigate(`/salaCirurgica/AssinaturaLimpeza`, {
					state: {
						TIPO: 'NOVA',
						CODIGO_LOCALIDADE: session.CODIGO_LOCALIDADE,
						CODIGO_AVISO: location.state.AVISO,
						CODIGO_SUB_LOCALIDADE: session.CODIGO_SUBLOCALIDADE,
						CODIGO_SALA_CIRURGICA: session.CODIGO_SALA_CIRURGICA,
						ETAPA: codigoEtapa,
						DATA: data,
						PAPEL_PERMITIDO: 222
					}
				})
				return true;
			}
		}


		return true;
	}

	const proximaCirurgia = async () => {
        console.log('proxima');
        setDados({
            state: {
                DADOS: {
                    COD_AVISO: location.state.AVISO,
                    PROX_ETAPA: location.state.CD_ETAPA,
                },
                CODIGO_SALA_CIRURGICA: session.CODIGO_SALA_CIRURGICA
            }
        })
        setAbrirModalAssinatura(true)
    }
   
    const setCodigoUsuario = codigo => {
        setAbrirModalAssinatura(false)
		
        const {AVISO: aviso} = location.state
		const {CODIGO_SALA_CIRURGICA: sala} = session
        
        API.patch(`/proxima-cirurgia`, {sala, aviso, usuario: codigo})
        .then( () => {
                console.log('proxima cirurgia retorno');
                navigate('/salaCirurgica/preparoLimpeza')
            });
    }

	return (
			<PageWrapper title={cirurgicalFlow.SALA_CIRURGICA_LIMPEZA.text}>
				<Header titulo={localidadeHeader} localidade='Preparo e Limpeza'>
					<Dropdown >
						<DropdownToggle>
							<Button color="light" icon="menu" isLight >
								<p> </p>
							</Button>
						</DropdownToggle>
						<DropdownMenu>
							<DropdownItem onClick={() => alterarLocalidade()}>
								Alterar Local
							</DropdownItem>
							<DropdownItem onClick={() => proximaCirurgia()}>
								Próxima Cirurgia
							</DropdownItem>
						</DropdownMenu>
					</Dropdown>
				</Header>
				{
					abrirModalAssinatura 
					? <AssinaturaUsuario location={dados} codigoUsuario={ e => setCodigoUsuario(e)} voltar={() => setAbrirModalAssinatura(false)} />
					: 
						<Page>
							<div className='row'>
								<div className="container" style={{ marginTop: '150px' }}>
									<Card borderSize="3" borderColor="primary" className="card-page">
										<div className='row' >
											<div className='col-sm-3 pull-right body-card ' >
												<div className='ready'>
													<h1 style={{ fontWeight: "bold" }}>Limpeza</h1>
												</div>
											</div>

											<div className='col-sm-8' style={{ padding: '15px' }}>
												<div className='row'>
													{
														// eslint-disable-next-line no-unused-vars
														perguntas.map((q, i) => (
															<div key={q.CD_ETAPA} className='col-sm-6 ' style={{ "maxWidth": "200px", margin: 'auto', textAlign: 'center' }}>
																<div className="row " >
																	<div className="col-sm-12">
																		{
																			i === 0 ?
																				// eslint-disable-next-line no-unneeded-ternary, no-unused-expressions
																				<Button  onClick={() => { (perguntas[0].DT_ETAPA === null) && assinar(q.CD_ETAPA)}} id={q.CD_ETAPA} style={{ fontWeight: '700', fontSize: '25px', color: "#fff", height: '130px', borderRadius: '30px', backgroundColor:  (perguntas[0].DT_ETAPA === null) ? '#FF7455' : '#E13813'}} color="light" size="large">{q.DS_ETAPA}</Button>

																			: i === 1 ?
																					// eslint-disable-next-line no-unneeded-ternary,no-unused-expressions
																					<Button  onClick={() => { (perguntas[0].DT_ETAPA === null) || (perguntas[1].DT_ETAPA === null) && assinar(q.CD_ETAPA)}} id={q.CD_ETAPA} style={{ fontWeight: '700',  color: "#fff", fontSize: '25px', height: '130px', borderRadius: '30px', backgroundColor: (perguntas[0].DT_ETAPA === null) ? '#97989B':perguntas[1].DT_ETAPA === null ? '#B2C0FF':'#4D6BF4'}} color="light" size="large">{q.DS_ETAPA}</Button>
																			: i === 2 &&
																					// eslint-disable-next-line no-unneeded-ternary,no-unused-expressions
																					<Button onClick={() => { (perguntas[1].DT_ETAPA === null) || (perguntas[2].DT_ETAPA === null) && assinar(q.CD_ETAPA)}} id={q.CD_ETAPA} style={{ fontWeight: '700',  color: "#fff", fontSize: '25px', height: '130px', borderRadius: '30px', backgroundColor: (perguntas[1].DT_ETAPA === null) ? '#97989B':perguntas[2].DT_ETAPA === null ? '#B2C0FF':'#4D6BF4' }} color="light" size="large">{q.DS_ETAPA}</Button>

																		}
																	</div>
																	<div className="col-sm-6" style={{ marginTop: '10px', width: "auto" }}>
																		<div >
																			{
																				i === 0 ?
																					<input style={{ width: '190px', height: '50px', borderRadius: '30px', backgroundColor: (perguntas[0].DT_ETAPA === null) ? '#FF7455' : '#E13813', textAlign: 'center', margin: 'auto', paddingTop: '1px', paddingLeft: '15px', fontWeight: 'bold', fontSize: '18px', color: 'white', border: '0px' }} className='input-time' onChange={(e) => { setInputInicioPreparo(e.target.value); confirmacaoModalData(q.CD_ETAPA, e.target.value) }} type="datetime-local" id="data-fim" name="date" defaultValue={q.DT_ETAPA} />

																					: i === 1 ?
																						// eslint-disable-next-line no-unneeded-ternary
																						<input disabled={(perguntas[0].DT_ETAPA != null) ? false : true} style={{ width: '190px', height: '50px', borderRadius: '30px', backgroundColor: (perguntas[0].DT_ETAPA === null) ? '#97989B':perguntas[1].DT_ETAPA === null ? '#B2C0FF':'#4D6BF4', textAlign: 'center', margin: 'auto', paddingTop: '1px', paddingLeft: '15px', fontWeight: 'bold', fontSize: '18px', color: 'white', border: '0px' }} className='input-time' onChange={(e) => { setInputInicioPreparo(e.target.value); confirmacaoModalData(q.CD_ETAPA, e.target.value) }} type="datetime-local" id="data-fim" name="date" defaultValue={q.DT_ETAPA} />
																						: i === 2 &&
																						// eslint-disable-next-line no-unneeded-ternary
																						<input disabled={(perguntas[1].DT_ETAPA != null) ? false : true} style={{ width: '190px', height: '50px', borderRadius: '30px', backgroundColor: (perguntas[1].DT_ETAPA === null) ? '#97989B':perguntas[2].DT_ETAPA === null ? '#B2C0FF':'#4D6BF4', textAlign: 'center', margin: 'auto', paddingTop: '1px', paddingLeft: '15px', fontWeight: 'bold', fontSize: '18px', color: 'white', border: '0px' }} className='input-time' onChange={(e) => { setInputInicioPreparo(e.target.value); confirmacaoModalData(q.CD_ETAPA, e.target.value) }} type="datetime-local" id="data-fim" name="date" defaultValue={q.DT_ETAPA} />


																			}

																		</div>
																	</div>
																	<div className="col-sm-10" style={{ marginTop: '10px', "position": "relative", left: "20px" }}>
																		<div style={{ "fontSize": "17px", "fontWeight": "bold" }} >
																			{i === 0 ?
																				q.CD_USUARIO_OPERACAO === null ?
																					<span style={{ "color": "transparent" }}>PENDENTE</span>
																					:
																					q.CD_USUARIO_OPERACAO
																				:
																				q.CD_USUARIO_OPERACAO === null ?
																					<span style={{ "color": "transparent" }}>PENDENTE</span>
																					:
																					q.CD_USUARIO_OPERACAO
																			}
																		</div>
																	</div>
																</div>
															</div>
														))
													}



												</div>

											</div>
										</div>
									</Card>
								</div>
							</div>



							<Modal id='alert' isCentered isAnimation={false} isOpen={modalAlerta} titleId='alert'>
								<ModalHeader style={{ backgroundColor: modalMessage.COR, color: "#fff" }}>
									<ModalTitle>{modalMessage.TITULO}</ModalTitle>
								</ModalHeader>
								<ModalBody className='ModalBody'>
									<h3>{modalMessage.MSG}</h3>
								</ModalBody>
								<ModalFooter>
									<ButtonGroup isToolbar="true" size='lg'>
										<Button color='light' onClick={() => setModalAlerta(false)}>
											FECHAR
										</Button>
									</ButtonGroup>
								</ModalFooter>
							</Modal>



						</Page>
				}
			</PageWrapper>
		
	)
}

export default Limpeza;